import React from 'react'
import { StaticQuery, Link, graphql } from 'gatsby'

import './style.css'

const { Fragment } = React

const NormalHomePage = () => (
	<StaticQuery
		query={graphql`
			query NormalHomePageQuery {
				allMarkdownRemark {
					edges {
						node {
							id
							html
							frontmatter {
								path
								title
								date
								author
							}
							excerpt
						}
					}
				}
			}
		`}
		render={data => {
			data.allMarkdownRemark.edges.sort((A, B) => {
				return new Date(B.node.frontmatter.date).getTime() - new Date(A.node.frontmatter.date).getTime()
			})

			return <Fragment>
				{data.allMarkdownRemark.edges.map(post => (
					<div className="normal-homepage-item" key={ post.node.id }>
						<h3><Link className="title" to={post.node.frontmatter.path}>{post.node.frontmatter.title}</Link></h3>
						<small>Posted by {post.node.frontmatter.author} on {post.node.frontmatter.date}</small>
						<br/>
						<br/>
						<div dangerouslySetInnerHTML={{__html: post.node.html.split('<!--more-->')[0]}}></div>
						<Link to={post.node.frontmatter.path}>Read More</Link>
						<br/>
					</div>
				))}
			</Fragment>
		}}
	/>
)

export default NormalHomePage

